import React, {useEffect} from 'react';
import {
    Link,
} from 'react-router-dom';
import { connect } from 'react-redux';
import notFoundImg from '../../assets/image/404.jpg';
import { changLoadingState } from '../../actions/global';
import './index.less';

const mapState = state => ({
    loading: state.global.isLoading,
})
export default connect(mapState, { changLoadingState })(function Index(props) {
    useEffect(() => {
        props.changLoadingState();
        return () => props.changLoadingState();
    }, []);

    return (
        <div className={'not-found-wrapper'}>
            <img className={'not-found-image'} src={notFoundImg} alt=""/>
            <p><Link to='/'>回到首页</Link></p>
        </div>
    );
})



